import React from 'react';
import { default as MuiPopper, PopperProps } from '@mui/material/Popper';

// Modified reason: the placeholder prop is mandatory, we need to verify it after @mui/material 5.15.12.
const Popper = React.forwardRef<HTMLElement, PopperProps>( ( props, ref ) => {
	return (
		<MuiPopper
			placeholder={ null }
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<PopperProps>;

export default Popper;
